import { NavContainer, StyledLink } from "./NavigationMenu.styled";

const NavigationMenu = () => {
  return (
    <NavContainer>
      <StyledLink to="/" ></StyledLink>
      <StyledLink to="/drinks"></StyledLink>
      <StyledLink to="/beer"></StyledLink>
      <StyledLink to="/snacks"></StyledLink>
      <StyledLink to="/promotions"></StyledLink>
      <StyledLink to="/contacts"></StyledLink>
    </NavContainer>
  );
};

export default NavigationMenu;
